body {
    font-family: sans-serif;
    font-size: 100%;
    line-height: 1.4em;
    margin: 0;
}


header {
    align-items: center;
    display: flex;
    font-size: 1.2rem;
    height: 100px;
    padding: 0 0 0 100px;
    p {
        font-weight: 700;
        margin: 0 20px 0 0;
    }
    nav {
        a {
            margin: 0 10px 0 0;
        }
    }
    .current {
      color: green;
    }
}

.content {
    padding: 20px 0 0 100px;
    p {
        max-width: 700px;
    }
}
